import React from 'react';
import {useState} from 'react';
import Modal from 'react-bootstrap/Modal';
import remove from '../images/remove.svg';
import './modal.css';
import Table from './Table';

function Remove() {
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    
    return (
      <>   
       
       <button onClick={handleShow} style={{border:"none"}}><img src={remove} alt=""/></button>
        
  
        <Modal
          show={show}
          onHide={handleClose}
          backdrop="static"
          keyboard={false}
        >
        <div className="removeStudent">
          <Modal.Header>
            <Modal.Title>Remove Student</Modal.Title>
          </Modal.Header>
          <Modal.Body>
           <strong className='remove-item'>Are you Sure You Want to remove the current student from the list?</strong>
          <div>
            <p className='remove-item'>Student Name</p>
            <span className='remove-item'>-</span>
            </div>

            <div>
            <p className='remove-item'>Class</p>
            <span className='remove-item'>-</span>
            </div>
          </Modal.Body>
          <Modal.Footer>
          <button class="btn outline-primary" onClick={handleClose}>Cancel</button>
           
            <button class="btn btn-danger">Remove</button>
          </Modal.Footer>
          </div>
        </Modal>
      </>
    );
  }
  
export default Remove;  