import React from 'react';
import './sidebar.css'
import dashboard from '../images/dashboard.svg'
import logo from '../images/logo3.svg'
import book from '../images/book 1.svg'
import standusers from '../images/stand-users 1.svg'
import exam from '../images/exam.svg'
import result from '../images/result 1.svg'
import blackboard from '../images/black-board 1.svg'
import live from '../images/live-streaming 1.svg'
import notifications from '../images/notification 1.svg'
import profile from '../images/profile.svg'


function Sidebar(){
   return(
    <div className="sidebar">
            <div style={{padding:"0 13px"}}>
            <div className="logo">
                <span className="logo-item"><img src={logo} alt=""/></span>
                <span className="logo-text logo-item"><strong>School Space</strong></span>
            </div>
            </div>

            <div>
                <ul>
                <li><img src={dashboard} alt="" /><a href="#dashboard">Dashboard</a></li>
                <li><img src={book} alt=""/><a href="#courses">Courses</a></li>
                <li class="active"><img src={standusers} alt=""/><a  href="#students">Students</a></li>
                <li><img src={exam} alt=""/><a href="#exams">Exams</a></li>
                <li><img src={result} alt=""/><a href="#exams">Results</a></li>
                <li><img  src={blackboard} alt=""/><a href="#exams">Notice Board</a></li>
                <li><img  src={live} alt=""/><a href="#exams">Live Classes</a></li>
                <li><img  src={notifications} alt=""/><a href="#exams">Notifications</a></li>
                </ul> 
            </div> 
            <div className='profile'>
                <img style={{marginBottom:"13px"}} src={profile} alt='' width="48px" height="48px" />
                <span><strong>Andy Samberg</strong></span>
                <span style={{color:"#7f878a"}}>andy.samberg@gmail.com</span>
                <button>View Profile</button>    
            </div>  
    </div>
   )
}

export default Sidebar;