import './table.css';
import React from 'react';
import Remove from './Remove';
import Edit from './Edit';

const data=[
    {number:1, name:"Robort Fox", class:"8th",result:"Passed",score:"78/100",Grade:"Excellent"}, 
    {number:2, name:"Ralph Edwards", class:"7th",result:"Failed",score:"20/100",Grade:"Poor"},
    {number:3, name:"Esther Howard", class:"9th",result:"Passed",score:"60/100",Grade:"Average"}, 
    {number:4, name:"Eleanor Pena", class:"8th",result:"Passed",score:"60/100",Grade:"Average"},
    {number:5, name:"Ariene McCoy", class:"7th",result:"Passed",score:"85/100",Grade:"Excellent"}, 
    {number:6, name:"Marvin Mckinney", class:"9th",result:"Failed",score:"25/100",Grade:"Poor"},
    {number:7, name:"Wade Warren", class:"7th",result:"Passed",score:"95/100",Grade:"Excellent"} 
]

function Table(){

    return(
            <table>
                <tr>
                    <th>No.</th>
                    <th>Student Name</th>
                    <th>Class</th>
                    <th>Result</th>
                    <th>Score</th>
                    <th>Grade</th>
                    <th></th>
                </tr>
                {data.map((val,key) =>{
                    return(
                        <tr>
                            <td>{val.number}</td>
                            <td>{val.name}</td>
                            <td>{val.class}</td>
                            <td>
                                {val.result==='Passed'?
                                (<span class="resultPassed">{val.result}</span>):
                                (<span class="resultFailed">{val.result}</span>)
                                }
                            </td>
                            <td>{val.score}</td>
                            {val.Grade==='Excellent'?(<td className='excellent'>{val.Grade}</td>):
                            (val.Grade)==='Average'?(<td className='average'>{val.Grade}</td>):
                            (<td className='poor'>{val.Grade}</td>)}
                            
                            <td>
                                <span class="hide">
                                <Edit />
                                   <Remove />
                                </span>
                            </td>
                            
                        </tr>
                    )
                })}
            </table>
    )
}

export default Table;